            function updateservice(id, stacurrent) {

                for (var j = 0; j < id.length; j++) {
                    for (var i = 0; i < vm.filteredTodos.length; i++) {
                        if (vm.filteredTodos[i]._id == id[j]) {

                            vm.filteredTodos[i].status = stacurrent;
                            //vm.selectedId = "";
                        }
                    }
                }
                $('#selectedids').val("");
                gethtmltr();
            }



