(function ($http, $scope, $location, $mdDialog, $filter)
{
    'use strict';
    angular
        .module('app.addclient')
        .controller('addclientController', addclientController);

    /** @ngInject */
    function addclientController(SampleData, $http, $scope, Documents, $location, $mdDialog, $filter) {

        var usertype = window.localStorage['type'];
        if (usertype == 'User' || usertype == undefined) {
            $location.path('Careers');
        }
        else if (usertype == "") {
            $location.path('login');
        }
        else {
            var vm = this;
            var url = window.localStorage['APIURL'];

            vm.notes = [];
            vm.count = 0;

            $scope.cancel = function () {
                $location.path('Client');
            }
            //add notes to json
            $scope.addnote = function () {

                vm.count++;
                vm.notes.push({ "id": vm.count, "title": vm.note, "createby": window.localStorage['storageName'], "date": new Date(), "type": window.localStorage['type'] });
                vm.note = "";
            }
            $scope.removenotes = function (id) {

                for (var i = 0; i < vm.notes.length; i++) {
                    if (vm.notes[i].id == id) {


                        var index = vm.notes.map(function (e) { return e.id; }).indexOf(id);

                        vm.notes.splice(index, 1);
                        vm.count--;
                    }
                }
            }

            //saveclient
            $scope.createclient = function () {

                saveclient();
            }

            //save a client
            function saveclient() {
                var teammember = [];
                if (vm.teamfirstname != undefined) {
                    teammember.push(vm.teamfirstname);
                }
                if (vm.teamlastname != undefined) {
                    teammember.push(vm.teamlastname);
                }
                if (vm.teamemail != undefined) {
                    teammember.push(vm.teamemail);
                }
                if (vm.TeamPhone != undefined) {
                    teammember.push(vm.TeamPhone);
                }
                if (vm.teamext != undefined) {
                    teammember.push(vm.teamext);
                }
                var ip = document.getElementById('ipAddressID').value;
                $http.post(url + 'client/Admin',
                                          {
                                              "firstname": vm.firstname,
                                              "lastname": vm.lastname,
                                              "username": vm.email,
                                              "secondaryemail": vm.secondaryemail,
                                              "title": vm.roletitle,
                                              "phone": vm.Phone,
                                              "extphone": vm.ext,
                                              "companyname": vm.CompanyName,
                                              "street": vm.StreetAddress,
                                              "city": vm.City,
                                              "state": vm.StateRegion,
                                              "country": vm.Country,
                                              "zip": vm.postalCode,
                                              "teammember": teammember,
                                              "notes": vm.notes,
                                              "IPaddress": ip,

                                          }).success(function (data, status) {

                                              if (data.status == "Success") {
                                                  $location.path('Client');
                                              }

                                              else {
                                                  $mdDialog.show(
                                   $mdDialog.alert()
                                     .parent(angular.element(document.querySelector('#popupContainer')))
                                     .clickOutsideToClose(true)
                                     .parent(angular.element(document.body))
                                     .title('Error!')
                                     .textContent(data.message)
                                     .ariaLabel('')
                                     .ok('Ok')
                                     .targetEvent()
                                 );
                                              }
                                          }).error(function (data) {

                                              console.log(data);
                                              $mdDialog.show(
                                  $mdDialog.alert()
                                    .parent(angular.element(document.querySelector('#popupContainer')))
                                    .clickOutsideToClose(true)
                                    .parent(angular.element(document.body))
                                    .title('Error!')
                                    .textContent('Error')
                                    .ariaLabel('')
                                    .ok('Ok')
                                    .targetEvent()
                                );
                                          });
            }
        }
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .service('fuseTheming', fuseThemingService);

    /** @ngInject */
    function fuseThemingService($cookies, $log, $mdTheming)
    {
        var service = {
            getRegisteredPalettes: getRegisteredPalettes,
            getRegisteredThemes  : getRegisteredThemes,
            setActiveTheme       : setActiveTheme,
            setThemesList        : setThemesList,
            themes               : {
                list  : {},
                active: {
                    'name' : '',
                    'theme': {}
                }
            }
        };

        return service;

        //////////

        /**
         * Get registered palettes
         *
         * @returns {*}
         */
        function getRegisteredPalettes()
        {
            return $mdTheming.PALETTES;
        }

        /**
         * Get registered themes
         *
         * @returns {*}
         */
        function getRegisteredThemes()
        {
            return $mdTheming.THEMES;
        }

        /**
         * Set active theme
         *
         * @param themeName
         */
        function setActiveTheme(themeName)
        {
            // If theme does not exist, fallback to the default theme
            if ( angular.isUndefined(service.themes.list[themeName]) )
            {
                // If there is no theme called "default"...
                if ( angular.isUndefined(service.themes.list.default) )
                {
                    $log.error('You must have at least one theme named "default"');
                    return;
                }

                $log.warn('The theme "' + themeName + '" does not exist! Falling back to the "default" theme.');

                // Otherwise set theme to default theme
                service.themes.active.name = 'default';
                service.themes.active.theme = service.themes.list.default;
                $cookies.put('selectedTheme', service.themes.active.name);

                return;
            }

            service.themes.active.name = themeName;
            service.themes.active.theme = service.themes.list[themeName];
            $cookies.put('selectedTheme', themeName);
        }

        /**
         * Set available themes list
         *
         * @param themeList
         */
        function setThemesList(themeList)
        {
            service.themes.list = themeList;
        }
    }
})();

(function ()
{
    'use strict';

    angular
        .module('app.core')
        .config(config);

    /** @ngInject */
    function config($mdThemingProvider, fusePalettes, fuseThemes)
    {
        // Inject Cookies Service
        var $cookies;
        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies)
            {
                $cookies = _$cookies;
            }
        ]);

        // Check if custom theme exist in cookies
        var customTheme = $cookies.getObject('customTheme');
        if ( customTheme )
        {
            fuseThemes['custom'] = customTheme;
        }

        $mdThemingProvider.alwaysWatchTheme(true);

        // Define custom palettes
        angular.forEach(fusePalettes, function (palette)
        {
            $mdThemingProvider.definePalette(palette.name, palette.options);
        });

        // Register custom themes
        angular.forEach(fuseThemes, function (theme, themeName)
        {
            $mdThemingProvider.theme(themeName)
                .primaryPalette(theme.primary.name, theme.primary.hues)
                .accentPalette(theme.accent.name, theme.accent.hues)
                .warnPalette(theme.warn.name, theme.warn.hues)
                .backgroundPalette(theme.background.name, theme.background.hues);
        });
    }

})();
